.loader {
  z-index: 1001;
  position: fixed;
  inset: 0;
}

.loader--hidden {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s .5s, opacity .5s linear;
}

.loader--visible {
  visibility: visible;
  opacity: 1;
  transition: opacity .5s linear;
}

.loader__circles1 {
  height: 100px;
  width: 100px;
  transform-origin: center;
  background-color: #0000;
  position: absolute;
  top: 50vh;
  left: 50%;
  transform: translate(-50%, -50%)rotate(45deg);
}

.loader__circles2 {
  height: 100px;
  width: 100px;
  background-color: #0000;
  position: absolute;
  top: 50vh;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader__circle {
  width: 25px;
  height: 25px;
  background-color: #46b9ff;
  border-radius: 50%;
  animation: 2s infinite bigcon;
  position: absolute;
  box-shadow: 0 2px #0000004d;
}

.loader__circle--ad1 {
  animation-delay: -1.75s;
}

.loader__circle--ad6 {
  animation-delay: -1.5s;
}

.loader__circle--ad2 {
  animation-delay: -1.25s;
}

.loader__circle--ad7 {
  animation-delay: -1s;
}

.loader__circle--ad3 {
  animation-delay: -.75s;
}

.loader__circle--ad8 {
  animation-delay: -.5s;
}

.loader__circle--ad4 {
  animation-delay: -.25s;
}

.loader__circle--ad5 {
  animation-delay: 0s;
}

.loader__circle:nth-child(1) {
  top: 0%;
  left: 0%;
}

.loader__circle:nth-child(2) {
  top: 0%;
  right: 0%;
}

.loader__circle:nth-child(3) {
  bottom: 0%;
  right: 0%;
}

.loader__circle:nth-child(4) {
  bottom: 0%;
  left: 0%;
}

.loader__bigcon {
  height: 95px;
  width: 95px;
  transform-origin: center;
  background-color: #0000;
  animation: 2s linear .25s infinite lightball;
  position: absolute;
  top: 50vh;
  left: 50%;
  transform: translate(-50%, -50%)rotate(-45deg);
}

.loader__lightball {
  width: 20px;
  height: 20px;
  z-index: 1;
  background-color: #54f7f8;
  border-radius: 50%;
  position: absolute;
  box-shadow: 0 0 10px #54f7f8, 0 0 20px #54f7f8, 0 0 30px #54f7f8, 0 0 50px #54f7f8, 0 0 60px #54f7f8;
}

@keyframes lightball {
  0% {
    transform-origin: center;
    transform: translate(-50%, -50%)rotate(45deg);
  }

  100% {
    transform-origin: center;
    transform: translate(-50%, -50%)rotate(405deg);
  }
}

@keyframes bigcon {
  0% {
    background-color: #46b9ff;
    transform: scale(1);
  }

  10% {
    background-color: #54f7f8;
    transform: scale(1.3);
  }

  15% {
    transform: scale(1);
  }

  25% {
    background-color: #46b9ff;
    transform: scale(1);
  }

  100% {
    background-color: #46b9ff;
    transform: scale(1);
  }
}

